*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;;
}

html,body{
  /* scroll-behavior: smooth; */
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.4rem;
}

code {
font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

::selection{
  color: #fff;
  background: rgba(0, 0, 0, 0.5);
}
.landing{
    background-color: #e27d60;
    /* padding-bottom: 4rem; */
    height: 100vh;
    /* width: 100%; */
}

.intro-title{
    align-items: center;
    justify-content: center;
    position: relative;
    top: 25%;
    text-align: center;    
    color: #eee;
    text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.8);
}

.title{
    text-align: center;
    font-size: 55px;
    margin-bottom: 1rem;
    font-weight: 300;
    text-transform: uppercase;
    /* text-shadow: 2px 2px 1px #00000080; */
}

.divider{
    /* height: 10px; */
    position: relative;
    left: 50%;
    width: 10%;
    margin-top: 3rem;
    transform: translateX(-50%);
    border: 2px solid #000;
}

.background-icon{
    left: 75%;
    top: 55%;
    position: absolute;
    font-size: 2rem;
    width: 250px;
    transform: rotate(15deg);
    /* box-shadow: 3px 3px 2px 2px #eee; */
    /* display: inline; */
}
.social-contact{
    margin: 2rem 0;
    
    position: relative;
    width: 35%;
    display: inline-block;
}
.social-icon{
    padding: 15px;
    display: inline-block;
    /* width: 100px;
    height: 100px; */
    font-size: 1.5rem;
    color: #eee;
    border: 1px solid #eee;
    border-radius: 5px;
    transition: 0.2s all ease-in;
}

.social-icon:hover{
    border: 1px solid #00000080;
    box-shadow: 1px 1px 1px 1px #00000080;
    
}

.intro-context{
    margin: 1rem 15% 2.5rem;
    font-size: 20px;
}

@media only screen and (max-width: 768px) {
    .intro-title{
        top: 25%;
        /* font-size: 2rem; */
    }
    .title{
        /* margin-top: 2rem; */
        margin-bottom: 3rem;
        font-size: 2.5rem;
        font-weight: 300;
    }
    .intro-title>h5{
        /* width: 75%; */
        padding: 0 20%;
        font-size: 1.2rem;
        font-weight: 400;
    }
    .social-contact{
        width: 100%;
        margin: 2rem 0;
        left: 50%;
        transform: translateX(-50%);
        position: relative;
    }
    .background-icon{
        width: 100px;
        left: 75%;
        top: 75%;
        display: relative;
    }

}

.aboutMe{
    min-height: 100vh;
    text-align: center;
    /* margin-bottom: 2rem; */
}

.heading{
    text-align: center;
    font-weight: 300;
    font-size: 55px;
    position: relative;
    padding-top: 5%;
}

.footer{
    font-size: 1rem;
    text-align: center;
    padding: 2rem;
    font-weight: 400;
    color: #eee;
    background-color: #e27d60;
}
